import Vue from 'vue'
import Router from 'vue-router'

import Layout from './views/layout/layout'
import loginPanel from './views/auth/login'
import aboutPage from './views/about/index'
import roomsPage from './views/rooms/index'
import contactPage from './views/contact/index'
import newsPage from './views/news/index'

Vue.use(Router)

export default new Router({
  routes: [{
      path: 'login',
      name: 'Login',
      component: loginPanel
    },
    {
      path: '/',
      name: 'Home',
      component: Layout,
      children: [
        {
          path: '/about',
          name: 'About',
          component: aboutPage
        },
        {
          path: '/rooms',
          name: 'Rooms',
          component: roomsPage
        },
        {
          path: '/contact',
          name: 'Contact',
          component: contactPage
        },
        {
          path: '/news',
          name: 'News',
          component: newsPage
        }
      ]
    },
  ],
  mode: 'history'
})

